import React, { Component, lazy, Suspense } from 'react'
import { HashRouter as Router, Route, Switch, Link, withRouter, Redirect } from 'react-router-dom';
import http from '../../utils/http'
import { notification } from 'antd';
const Category = lazy(() => import('./Category'))
const AddArticle = lazy(() => import('./Article'))
const User = lazy(() => import('./User'))
const Tag = lazy(() => import('./Tags'))
const ArticleList = lazy(() => import('./ArticleList'))
const Home = lazy(() => import('./Home'))

const fakeAuth = {
  isAuthenticated: false,
  authenticate(cb) {
    fakeAuth.isAuthenticated = true;
    setTimeout(cb, 100); // fake async
  },
  signout(cb) {
    fakeAuth.isAuthenticated = false;
    setTimeout(cb, 100);
  }
};
function PrivateRoute({ children, ...rest }) {
  return (
    <Route
      {...rest}
      render={({ location }) =>
        fakeAuth.isAuthenticated ? (
          children
        ) : (
            <Redirect
              to={{
                pathname: "/login",
                state: { from: location }
              }}
            />
          )
      }
    />
  );
}
class RouterGuard extends Component {
  constructor(props) {
    super()
  }
  componentWillMount() {
    this.getUserStatus()
    console.log('路由跳转前的拦截', this.props)
  }

  getUserStatus() {
    let { history, location } = this.props
    let { from } = location.state || { from: { pathname: location.pathname } };
    let login = () => {
      fakeAuth.authenticate(() => {
        history.replace(from);
      });
    };
    let unlogin = () => {
      fakeAuth.signout(() => {
        history.replace('/login');
      });
    };

    console.log('准备渲染compoent前', this.props)
    http.get('/user/currentUser').then(({ data }) => {
      if (data.code === 0) {
        login();
      } else {
        unlogin()
      }
    }).catch((error) => {
      if (error.status) {
        if (error.status === 401) {
          notification.error({
            message: '提示',
            description: error.data.message,
            duration: 3
          });
          fakeAuth.signout();
        }
      }
    })
  }
  render() {
    let props = this.props;
    return (
      <div>
        <Suspense fallback={<div></div>}>
          <Switch>
            <PrivateRoute path="/article/addArticle">
              <AddArticle {...props} />
            </PrivateRoute>
            <PrivateRoute path="/tag" exact>
              <Tag />
            </PrivateRoute>
            <PrivateRoute path="/article/articleList" exact>
              <ArticleList props={props} />
            </PrivateRoute>
            <Route path="/category" exact component={Category} />
            <Route path="/admin/user" exact component={User} />
            <Route path="/" exact component={Home} />
          </Switch>
        </Suspense>
      </div>

    )
  }
}
export default withRouter(RouterGuard)