import { Layout, Menu, Skeleton, Icon } from 'antd';
import React, { useState } from 'react'
import './index.scss'
import { Link } from 'react-router-dom';
import PageHeader from '../../component/pageHeader'
import RouterGuard from './route'
const { Content, Footer, Sider, Header } = Layout;
const { SubMenu } = Menu;

const Admin = (props) => {
  const [collapsed, setCollapsed] = useState(false);
  const [address, setAddress] = useState('');
  const onCollapse = collapsed => {
    setCollapsed(collapsed)
  };
  const pathname = props.location.pathname || 'index';
  console.log(pathname)
  return (
    <Layout style={{ minHeight: '100vh' }}>
      <Sider collapsible
        collapsed={collapsed}
        onCollapse={onCollapse}>
        <div className="logo" />
        <Menu theme="dark" defaultSelectedKeys={[pathname]} mode="inline"
          defaultOpenKeys={['sub1', 'sub2']}
          onClick={({ key }) => { setAddress(key) }}
        >
          <Menu.Item key="/">
            <Icon type="home" />
            <span>首页</span>
            <Link to="/"></Link>
          </Menu.Item>
          <Menu.Item key="/category">
            <Icon type="bars" />
            <span>分类</span>
            <Link to="/category"></Link>
          </Menu.Item>
          <Menu.Item key="/tag">
            <Icon type="tags" />
            <span>标签</span>
            <Link to="/tag"></Link>
          </Menu.Item>
          <SubMenu
            key="sub1"
            title={
              <span>
                <Icon type="book" />
                <span>文章</span>
              </span>
            }
          >
            <Menu.Item key="/article/addArticle"><Icon type="file-add" />新建文章<Link to="/article/addArticle"></Link></Menu.Item>
            <Menu.Item key="/article/articleList"><Icon type="unordered-list" />文章列表<Link to="/article/articleList"></Link></Menu.Item>
          </SubMenu>
          <SubMenu
            key="sub2"
            title={
              <span>
                <Icon type="team" />
                <span>用户管理</span>
              </span>
            }
          >
            <Menu.Item key="/admin/user">用户列表<Link to="/admin/user"></Link></Menu.Item>
          </SubMenu>
          <Menu.Item key="9">
            <Icon type="file" />
            <span>File</span>
          </Menu.Item>
        </Menu>
      </Sider>
      <Layout>
        <PageHeader props={props} />
        <Content style={{ margin: ' 16px' }}>
          <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
            <RouterGuard />
          </div>
        </Content>

        <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer>
      </Layout>
    </Layout>
  );
}

export default Admin;