import React, { useState, useEffect } from 'react'
import http from '@/utils/http'
import {
  Modal,
  Card,
  Form,
  Input,
  Select,
  Upload,
  Icon,
  message,
  Row,
  Col,
  InputNumber
} from 'antd'
import { get } from 'http';
const { Option } = Select;
const { TextArea } = Input;
/**
 * 注册layout样式
 */
const formItemLayout = {
  labelCol: {
    xs: { span: 10 },
    sm: { span: 4 }
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 16 }
  },
};
/**
 * 图像上传
 * @param {*} img 
 * @param {*} callback 
 */
function getBase64(img, callback) {
  const reader = new FileReader();
  reader.addEventListener('load', () => callback(reader.result));
  reader.readAsDataURL(img);
}




const RegisterModal = (props) => {
  const { visible, setVisible, form } = props;
  const { getFieldDecorator, validateFields } = form;
  const [uploadLoading, setUpload] = useState(false);
  const [imageUrl, setImageUrl] = useState();
  const [avatar, setAvatar] = useState(null);
  const [captcha, setCaptcha] = useState(null);
  const [introduce, setIntroduce] = useState('');
  const [confirmDirty, setConfirmDirty] = useState(false);
  useEffect(() => {
    if (visible === true) {
      getSvgCaptcha()
    }
  }, [visible])
  function getSvgCaptcha() {
    http({
      method: 'get',
      url: '/user/getsvgcaptcha'
    }).then(async res => {
      await setCaptcha(res.data)
    })
  }
  function beforeUpload(file) {
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
    if (!isJpgOrPng) {
      message.error('您仅能上传 JPG/PNG 格式的图片作为头像!');
      return false;
    }
    const isLt5M = file.size / 1024 / 1024 < 5;
    if (!isLt5M) {
      message.error('图片的大小不得大于 5MB!');
      return false;
    }
    setAvatar(file);
    getBase64(file, async imageUrl => {
      setUpload(false);
      setImageUrl(imageUrl)
    })
    return false;
  }
  /**
   * 表单提交函数
   */
  const handleSubmit = (e) => {
    e.preventDefault();
    form.validateFieldsAndScroll((err, values) => {
      console.log(err, values)
      if (!err) {
        console.log('Received values of form: ', values);
        let formData = new FormData();
        formData.append('username', values.username);
        formData.append('password', values.password);
        formData.append('captcha', values.captcha);
        formData.append('introduce', introduce);
        formData.append('phone', values.prefix + values.phone);
        if (avatar !== null) {
          formData.append('avatar', avatar);
        }
        http({
          method: 'post',
          headers: { "Content-Type": "multipart/form-data;boundary=" + new Date().getTime() },
          url: '/user/register',
          data: formData
        }).then(({ data }) => {
          if (data.code === 0) {
            setVisible(false);
            message.success(data.message + '稍后将自动跳转至后台页面');
            props.checkLogin({
              username: values.username,
              password: values.password
            })
          } else {
            message.error(data.message);
            getSvgCaptcha();
          }
        })
      }
    });
  }
  /**
   * 手机号默认地区前缀
   */
  const prefixSelector = getFieldDecorator('prefix', {
    initialValue: '86',
  })(
    <Select style={{ width: 70 }}>
      <Option value="86">+86</Option>
      <Option value="87">+87</Option>
    </Select>,
  );
  /**
   * 用户名校验 
   */
  const validateToUsername = (rule, value, callback) => {
    if (!/^[A-Za-z0-9_-]{3,8}$/.test(value)) {
      callback('用户名格式错误，仅支持数字字母下划线');
    }
    callback();
  }
  /**
   * 密码校验
   */
  const handleConfirmBlur = e => {
    const { value } = e.target;
    let data = confirmDirty || value;
    setConfirmDirty(data);
  };
  const validateToNextPassword = (rule, value, callback) => {
    if (value && confirmDirty) {
      form.validateFields(['confirm'], { force: true });
      if (value.length < 8) {
        callback('密码长度不能小于8')
      }
      if (value.length > 16) {
        callback('密码长度不能大于16')
      }
      if (!/^[a-z0-9_-]{8,16}$/.test(value)) {
        callback('密码格式有误')
      }
    }
    callback();
  };
  const compareToFirstPassword = (rule, value, callback) => {
    if (value && value !== form.getFieldValue('password')) {
      callback('两次输入的密码不一致!');
    } else {
      callback();
    }
  };
  /**
   * 手机号校验
   */
  const validateToPhone = (rule, value, callback) => {
    if (value) {
      if (!/^\d*$/.test(value)) {
        callback('请输入数字类型的手机号!');
      }
      if (value.length > 0 && value.length < 11) {
        callback('请输入正确长度的手机号');
      }
      if (!/^1[34578]\d{9}$/.test(value)) {
        callback('手机号填写错误!')
      }
    }
    callback()

  }
  /**
   * 头像上传
   */
  const uploadButton = (
    <div>
      <Icon type={uploadLoading ? 'loading' : 'plus'} />
      <div className="ant-upload-text">Upload</div>
    </div>
  );

  return (
    <Modal visible={visible}
      onCancel={() => setVisible(false)}
      cancelText="取消"
      okText="注册"
      width="70%"
      destroyOnClose="true"
      onOk={handleSubmit}
    >
      <Form {...formItemLayout} >
        <Form.Item label="用户名">
          {getFieldDecorator('username', {
            rules: [
              {
                required: true,
                message: '请输入用户名'
              },
              {
                validator: validateToUsername
              }
            ],
          })(<Input placeholder="用户名3-8位，仅支持字母、数字、下划线" maxLength={8} />)}
        </Form.Item>
        <Form.Item label="密码" hasFeedback>
          {getFieldDecorator('password', {
            rules: [
              {
                required: true,
                message: '请输入8-16位的密码',
              },
              {
                validator: validateToNextPassword,
              },
            ],
          })(<Input.Password placeholder="密码长度为8-16位，仅支持字母、数字、下划线" maxLength={16} />)}
        </Form.Item>
        <Form.Item label="重复密码" hasFeedback>
          {getFieldDecorator('confirm', {
            rules: [
              {
                required: true,
                message: '请二次输入相同的密码!',
              },
              {
                validator: compareToFirstPassword
              },
            ],
          })(<Input.Password onBlur={handleConfirmBlur} maxLength={16} />)}
        </Form.Item>
        <Form.Item label="头像" extra="请选择一张图片设为你的头像">
          <Upload
            name="avatar"
            listType="picture-card"
            className="avatar-uploader"
            showUploadList={false}
            defaultFileList={imageUrl}
            beforeUpload={beforeUpload}
          >
            {imageUrl ? <img src={imageUrl} alt="avatar" style={{ width: '100%' }} /> : uploadButton}
          </Upload>
        </Form.Item>

        <Form.Item label="手机号">
          {getFieldDecorator('phone', {
            rules: [
              {
                required: true,
                message: '请输入手机号!'
              },
              {
                validator: validateToPhone
              }
            ],
          })(<InputNumber addonBefore={prefixSelector} maxLength={11} style={{ width: '100%' }} />)}
        </Form.Item>
        <Form.Item label="个人简介">
          <TextArea style={{ width: '100%' }} onChange={(e) => { setIntroduce(e.target.value) }} />
        </Form.Item>
        <Form.Item label="验证码" >
          <Row gutter={{ xs: 8, sm: 16, md: 24 }} type="flex" align="top">
            <Col xs={24} sm={16} md={16} lg={8}>
              {getFieldDecorator('captcha', {
                rules: [{
                  required: true,
                  message: '请输入验证码!'
                }
                ],
              })(<Input maxLength={4} />)}
              <div style={{ color: 'red' }}>看不清？点击图片换一张</div>
            </Col>
            <Col xs={24} sm={8} md={8} lg={10}>
              <span onClick={getSvgCaptcha} dangerouslySetInnerHTML={{ __html: captcha }}></span>
            </Col>
          </Row>
        </Form.Item>
      </Form>
    </Modal>
  )
}

export default React.memo(Form.create({})(RegisterModal));