import React, { useState } from 'react'
import { Card, Input, Icon, Button, Spin, Row, Col, message, notification } from 'antd';
import http from '@/utils/http'
import RegisterModal from './RegisterModal'
import './index.scss'
const Login = (props) => {
  const [isLoading, setLoad] = useState(false);
  const [userName, setUserName] = useState('');
  const [passWord, setPassword] = useState('');
  const [showRegister, setRegister] = useState(false);
  const checkLogin = (form) => {
    let data = {
      username: form.username || userName,
      password: form.password || passWord
    };
    setLoad(true)
    http({
      method: 'post',
      url: '/user/login',
      data
    }).then(({ data }) => {
      setLoad(false)
      if (data.code === 0) {
        message.success(data.message);
        props.history.push('/')
        React.$auth = data.data.type;
      } else {
        message.error(data.message);
      }
    }).catch(err => {
      notification.error({ message: '未知错误' });
      setLoad(false)
    })
  }
  return (
    <div className='login-page'>
      <div className="login-div">
        <RegisterModal visible={showRegister} setVisible={setRegister} checkLogin={checkLogin}/>
        <Spin tip="Loading..." spinning={isLoading} size="large">
          <Card title="我的小仓库" bordered={true} style={{ width: '100%', backgroundColor: "#f0f0f0", textAlign: "center" }} >
            <Input
              id="userName"
              size="large"
              placeholder="请输入用户名"
              prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
              onChange={(e) => { setUserName(e.target.value) }}
            />
            <br /><br />
            <Input.Password
              id="password"
              size="large"
              placeholder="请输入密码"
              prefix={<Icon type="key" style={{ color: 'rgba(0,0,0,.25)' }} />}
              onChange={(e) => { setPassword(e.target.value) }}
            />
            <br /><br />
            <Row gutter={2}>
              <Col span={12}>
                <Button type="primary" block onClick={checkLogin} > 登陆 </Button>
              </Col>
              <Col span={12}>
                <Button type="primary" block onClick={() => { setRegister(true) }} > 注册</Button>
              </Col>
            </Row>

          </Card>
        </Spin>
      </div>
    </div>
  )
}

export default Login;