import { HashRouter as Router, Route, Switch, Link, withRouter } from 'react-router-dom';
import { Popconfirm, message } from 'antd'
import { Breadcrumb } from 'antd';
import React from 'react'
import './index.scss'
import http from '@/utils/http'
const breadcrumbNameMap = {
  '/article': '文章管理',
  '/article/addArticle': '新建文章',
  '/category': '分类',
  '/tag': '标签管理',
  '/admin': '用户管理',
  '/admin/user': '用户列表',
  '/apps/1/detail': 'Detail',
  '/article/articleList': '文章列表',
};
const PageHeader = withRouter(props => {
  const { location } = props;
  const pathSnippets = location.pathname.split('/').filter(i => i);
  const extraBreadcrumbItems = pathSnippets.map((_, index) => {
    const url = `/${pathSnippets.slice(0, index + 1).join('/')}`;
    return (
      <Breadcrumb.Item key={url}>
        <Link to={url}>{breadcrumbNameMap[url]}</Link>
      </Breadcrumb.Item>
    );
  });
  const BreadcrumbItems = [
    <Breadcrumb.Item key={'主页'}>
      <Link to='/'>主页</Link>
    </Breadcrumb.Item>
  ].concat(extraBreadcrumbItems)
  const getCookie = function (name) {
    let arr;
    let reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
    if (arr = document.cookie.match(reg))
      return unescape(arr[2]);
    else
      return null;
  };
  const del = () => {
    http({
      method: 'get',
      url: '/user/loginOut'
    }).then(({ data }) => {
      if (data.code === 0) {
        message.success(data.message);
        let exp = new Date();
        exp.setTime(exp.getTime() - 1);
        document.cookie = 'username=;expires=' + exp.toGMTString();
        props.history.push('/login')
      }
    })
  }
  let username = getCookie('username');
  const UserInfo = () => (
    username !== null ? <div>
      <span>{`用户名:` + username}</span>&nbsp;&nbsp;
      <Popconfirm title="确定注销吗？"
        okText="确定"
        placement="bottomRight"
        onConfirm={() => { del() }}
        cancelText="取消">
        <a href="#">注销</a>
      </Popconfirm>
    </div> : <div><a href="/login">请登录!</a></div>
  )
  return (
    <div className="ant-page-header">
      <Breadcrumb>{BreadcrumbItems}</Breadcrumb>
      <div className="head-login">
        <UserInfo />
      </div>
    </div>
  );
});

export default PageHeader;